import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AppUtility } from '../../../../core/services/utility.services';
import { DownloadFinanceModel } from '../../models/download-finance.model';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { AlertService } from 'src/app/core/services/alert.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { DownloadFinanceService } from '../../http/download-finance.service';
import * as moment from 'moment';
import { CustomValidator } from '../../../../core/services/custom.validators';
import { BaseGrid } from '../../../../shared/components/basegrid';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { PageChangeEvent } from '@progress/kendo-angular-grid';
