import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class QboConnectionService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
  }

  public GetQBOConnectionDetails(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetQBOConnectionDetails`;
    return this.httpExt.post(url, reqdata, null);
  }
  
  public SaveQBOConnectionDetails(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveQBOConnectionDetails`;
    return this.httpExt.post(url, reqdata, null);
  }
}
