import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { FinanceComponent } from './finance.component';
import { UploadFinanceComponent } from './components/upload-finance/upload-finance.component'
import { DownloadFinanceComponent } from './components/download-finance/download-finance.component'
import { QboConnectionDetailsComponent } from './components/qbo-connection-details/qbo-connection-details.component'
import { FinanceMappingComponent } from './components/finance-mapping/finance-mapping.component';
import { DownloadUploadDefaultsComponent } from './components/download-upload-defaults/download-upload-defaults.component'
import { FinanceSeriesComponent } from './components/finance-series/finance-series/finance-series.component';
import { FinanceSeriesManageComponent } from './components/finance-series/finance-series-manage/finance-series-manage.component';
// import {TypeMasterList1099Component} from './components/1099-type-master/type-master-list1099/type-master-list1099.component';
// import {TypeMasterManage1099Component} from './components/1099-type-master/type-master-manage1099/type-master-manage1099.component'


const routes: Routes = [
  {
  path: '',
      component: FinanceComponent,
      children: [
        { path: '', redirectTo: 'upload-finance', pathMatch: 'full' },
        { path: 'upload-finance', component: UploadFinanceComponent, canActivate: [AuthGuard] },
        { path: 'download-finance', component: DownloadFinanceComponent, canActivate: [AuthGuard] },
        { path: 'qbo-connection', component: QboConnectionDetailsComponent, canActivate: [AuthGuard] },
        { path: 'finance-mapping', component: FinanceMappingComponent, canActivate: [AuthGuard] },
        { path: 'download-upload-defaults', component: DownloadUploadDefaultsComponent, canActivate: [AuthGuard] },
        { path: 'finance-series', component: FinanceSeriesComponent, canActivate: [AuthGuard] },
        { path: 'finance-series/manage', component: FinanceSeriesManageComponent, canActivate: [AuthGuard] },
        // { path: '1099-type-master', component: TypeMasterList1099Component, canActivate: [AuthGuard] },
        // { path: '1099-type-manage/manage', component: TypeMasterManage1099Component, canActivate: [AuthGuard] }
      ]  
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
  
})
export class FinanceRoutingModule { }
