import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';


@Injectable({
  providedIn: 'root'
})
export class DownloadUploadDefaultService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
  }

  public BMGFGetUpldDnldDefaults(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetUpldDnldDefaults`;
    return this.httpExt.post(url, reqdata, null);
  }
  
  public BMGFSaveUpldDnldDefaults(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveUpldDnldDefaults`;
    return this.httpExt.post(url, reqdata, null);
  }

}
