import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class FinanceMappingService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
  }

  public bmgfGetFinanceMapping(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetFinanceMapping`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmgfSaveFinanceMapping(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveFinanceMapping`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmgfDeleteFinanceMappingLine(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteFinanceMappingLine`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmgfGetFinLookupForFinMapping(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetFinLookupForFinMapping`;
    return this.httpExt.post(url, reqdata, null);
  }

}
